<template>
    <div class="category-info">
        <van-sticky class="top">
            <ul>
                <li :class="{active: current === index }" v-for="(item, index) in typeList" :key="index" @click="chooseType(index)">{{item.label}}</li>
            </ul>
            <ul class="sub" >
                <li :class="{active: curTab === 0}" @click="chooseAll()">全部</li>
                <li :class="{active: (curTab-1) === index}" @click="chooseMinor(index, item)" v-for="(item, index) in minsList" :key="index">{{item}}</li>
            </ul>
        </van-sticky>
        <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
            class="category-list"
        >
            <div
                v-for="(item, index) in list"
                :key="index"
                class="category-list-item"
                @click="toBookInfo(item._id)"
            >
                <div class="item-top">
                    <img :src="item.cover | fixImg" />
                </div>
                <div class="item-bottom">
                    <h2 class="textOverflow1">{{item.title}}</h2>
                    <p class="textOverflow2">{{item.shortIntro}}</p>
                    <div>
                        <span><i>{{(item.latelyFollower/10000).toFixed(1)}}万</i> 人气</span>
                        <span><i>{{item.retentionRatio.toFixed(1)}}%</i> 留存</span>
                    </div>
                </div>
            </div>
        </van-list>
    </div>
</template>

<script>
import { fetchsubCategories, fetchcategoryInfo } from '@/api/categories'
export default {
    name: 'CategoryInfo',
    data() {
        return {
            active: 0,
            minsList: [],
            gender: this.$route.query.gender,
            queryList: {
                // start: 0,
                // limit: 10,
                type: "hot",
                gender: this.$route.query.gender,
                major: this.$route.query.major,
                minor: ''
            },
            typeList: [
                { label: '热门', type: 'hot' },
                { label: '新书', type: 'hot' },
                { label: '好评', type: 'hot' },
                { label: '完结', type: 'hot' },
                { label: 'VIP', type: 'hot' }
            ],
            list: [],
            loading: false,
            finished: false,
            current: 0,
            curTab: 0
        };
    },
    // created () {
    //     this.getMsg()
    // },
    methods: {
        async getMsg() {
            await fetchsubCategories(this.$route.query.gender).then(res => {
                    for(let key in res.data){ 
                        if(key == this.gender) {
                            let tmp = res.data[key]
                            for(let i=0; i<tmp.length; i++) {
                                if(tmp[i].major === this.$route.query.major) {
                                    this.minsList = tmp[i].mins
                                }
                            }
                        }
                    }
                })
            // await fetchcategoryInfo(this.queryList).then(res => {
            //     console.log(res.data)
            // })
            await setTimeout(() => {
                fetchcategoryInfo(this.queryList).then(res => {
                    this.list = []
                    let temp = res.data.books
                    for (let i = 0; i < 10; i++) {
                        this.list.push(temp[i]);
                    }
                    // 加载状态结束
                    this.loading = false;
                    // 数据全部加载完成
                    if (this.list.length >= temp.length) {
                        this.finished = true;
                    }
                })
            }, 500)
        },
        toBookInfo(id) {
            this.$router.push({name: 'BookInfo', params: {id: id}})
        },
        onLoad() {
            this.getMsg()
        },
        chooseType(index) {
            this.current = index
        },
        chooseAll() {
            this.curTab = 0
            this.queryList.minor = ''
            this.getMsg()
        },
        chooseMinor(index, minor) {
            this.curTab = index + 1
            this.queryList.minor = minor
            this.getMsg()
        },
        watch: {
        '$route' () {
            if(this.$route.query.minor) {
                this.getMsg()
            }
        }
    }
    }
}
</script>

<style lang="scss" scoped>
.category-info {
    .top {
        ul {
            width: 100%;
            overflow: auto;
            border-bottom: 1px solid #ddd;
            padding: 10px;
            background-color: #fff;
            li {
                float: left;
                padding: 5px;
                margin-right: 10px;
                color: #999;
                font-size: 12px;
            }
            .active {
                border-radius: 10px;
                color: #ee0a24;
                border: 1px solid #ee0a24;
                font-size: 13px;
            }
        }
    }
    .category-list {
        padding: 0 10px;
        .category-list-item {
            display: flex;
            justify-content: start;
            padding: 10px 0;
            .item-top {
                img {
                    width: 75px;
                    height: 100px;
                    box-shadow: 0 2px 5px #ccc;
                }
            }
            .item-bottom {
                margin-left: 10px;
                h2 {
                    font-size: 16px;
                    font-weight: 700;
                    line-height: 22px;
                    padding-bottom: 10px;
                }
                p { 
                    line-height: 20px;
                    color: #999;
                }
                div {
                    padding-top: 10px;
                    color: #999;
                    span {
                        display: inline-block;
                        width: 50%;
                        i {
                        color: #ee0a24;
                    }
                    }
                }
            }
        }
    }
    
}
</style>